import React, {useEffect, useRef} from 'react';
import {
  FlatList,
  View,
  Text,
  StyleSheet,
  Linking,
  TouchableOpacity,
} from 'react-native';
import {Divider, useTheme} from 'react-native-paper';
import {useSelector} from 'react-redux';
import moment from 'moment';
import Labels from '../components/Labels';
import {IApplicationState} from '../redux/appReducer';
import Title from '../components/Title';
import {Issue} from '../redux/repo/reducer';
import Open from '../assets/svgs/open.svg';
import Closed from '../assets/svgs/closed.svg';
import Pagination from '../components/Pagination';

const GithubIssuesScreen = () => {
  const theme = useTheme();
  const refList = useRef<FlatList>(null);
  const {issues, page} = useSelector((state: IApplicationState) => state.repo);
  const {
    form: {values},
  } = useSelector((state: IApplicationState) => state.form);

  useEffect(() => {
    refList?.current?.scrollToOffset({offset: 0});
  }, [page]);

  const renderItem = ({item}: {item: Issue}) => (
    <TouchableOpacity onPress={() => Linking.openURL(item.html_url)}>
      <View style={[{marginHorizontal: theme.spacing.m}, styles.row]}>
        <View style={{top: theme.spacing.xs}}>
          {item.state === 'open' ? <Open /> : <Closed />}
        </View>
        <View style={{marginLeft: theme.spacing.m}}>
          <Title>{item.title}</Title>
          {item.labels && item.labels.length > 0 && (
            <Labels items={item.labels} />
          )}
          <Text
            style={[
              theme.variants.info,
              {color: theme.colors.text, marginTop: theme.spacing.s},
            ]}>
            #{item.number} opened {moment(item.created_at).fromNow()} by{' '}
            {item.user.login}
          </Text>
        </View>
      </View>
    </TouchableOpacity>
  );

  const ItemSeparatorComponent = () => (
    <Divider
      style={{
        marginVertical: theme.spacing.m,
        backgroundColor: theme.colors.divider,
      }}
    />
  );

  return (
    <View>
      {values && (
        <Text
          style={[
            theme.variants.h1,
            {margin: theme.spacing.m, color: theme.colors.primary},
          ]}>{`${values.organization}/${values.repository}`}</Text>
      )}

      <FlatList
        ref={refList}
        keyExtractor={item => item.node_id}
        contentContainerStyle={styles.container}
        ItemSeparatorComponent={ItemSeparatorComponent}
        ListFooterComponent={Pagination}
        renderItem={renderItem}
        data={issues[page]}
      />
    </View>
  );
};

export default GithubIssuesScreen;

const styles = StyleSheet.create({
  row: {
    flexDirection: 'row',
  },
  container: {
    paddingBottom: 100,
  },
});
